import React from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import { theme } from '../../theme';
import { TimePostItem } from '../Util/Time';
import { TagPostItem } from '../Util/Tag';

const PostTitle = styled.span`
    font-family: "SourceHanSerifCN";
    color: ${theme.colors.primary};
    font-size: 1.7rem;
    font-weight: 700;

    @media (max-width: 850px) {
        /* color:  ${theme.colors.tertiary}; */
        font-size: 1.3rem;
    }
`;

const PostContent = styled.p`
    line-height: 1.7rem;
    font-size: 1.05rem;
    color: ${theme.colors.text};
    margin: .8rem 0 .7rem 0;
    width: 100%;
    text-align: justify;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;

    @media (max-width: 850px) {
        margin: .5rem 0 .5rem 0;
        font-size: .95rem;
        line-height: 1.6rem;
    }
`;

const PostItemContainer = styled.div`
    padding: 0rem 1.7rem;
    margin: 1rem 0 3.2rem 0;
    border-left: 2.5px solid ${theme.colors.secondary};

    text-align: left;

    &:hover {
        /* border-left: 2px solid ${theme.colors.secondary}; */
        /* ${PostTitle} {
            text-decoration: underline 1px;
            text-underline-offset: .4rem;
            text-decoration-color:  ${theme.colors.tertiary};
        } */
    }

    @media (max-width: 850px) {
        padding: 1.1rem 0 1.4rem;
        margin: 0;
        border-left: none;
        border-bottom: 1px solid #80808030;

        &:hover {
            border-left: none;
        }
    }
`;

const ResponsiveForPC = styled.div`
    @media (max-width: 850px) {
        ${TagPostItem} {
            visibility: hidden;
            height: 0;
        }
    }
`;

interface PostItemProps {
    title: string;
    id: string;
    tags: string[];
    summary: string;
    time: string;
}


const PostItem: React.FC<PostItemProps> = ({ title, summary, id, tags, time }) => {
    return (
        <PostItemContainer>
            <Link to={`/post/${id}`}>
                <ResponsiveForPC>
                    <PostTitle>{title}</PostTitle>
                    {tags.map((tag: any, index: any) => (
                        <TagPostItem key={index}>{tag}</TagPostItem>
                    ))}
                </ResponsiveForPC>
                <PostContent>{summary}</PostContent>
                <TimePostItem>{time.split(' ')[0]}</TimePostItem>
            </Link>
        </PostItemContainer>
    );
};

export default PostItem;
